import controller from "../controllers/major.controller";
import { Router } from "express";
import auth from "../middleware/auth";
const router = Router();

router.get("/", controller.findAll);
router.get("/:id", controller.findOne);
router.post("/", controller.create);
router.put("/:id", controller.update);
router.delete("/:id", controller.remove);

router.get("/form3/:courseId", controller.getCourseIdForm3);
router.put("/form3/:courseId", controller.updateCourseIdForm3);
router.delete("/form3/:courseId", controller.deleteCourseIdForm3);

router.post("/form3", controller.form3);

export default router;
